/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { Button, Popover, TextControl } from '@wordpress/components';
import { useDispatch, useSelect } from '@wordpress/data';
import { useState } from '@wordpress/element';
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { QuotaMeter, HelpIcon, ConfirmationDialog } from '@nab/components';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type { SiteId } from '@nab/types';

export type AgencyPlanProps = {
	readonly siteId: SiteId;
};

export const AgencyPlan = ( { siteId }: AgencyPlanProps ): JSX.Element => {
	const productDisplay = useSelect( ( select ) =>
		select( NAB_DATA ).getProductDisplay()
	);

	return (
		<div className="nab-account-container__box nab-plan">
			<div className="nab-plan__content">
				<h3 className="nab-plan__title">
					{ productDisplay }
					<span className="nab-plan__period">
						{ _x( 'Agency Plan', 'text', 'nelio-ab-testing' ) }
					</span>

					<HelpIcon
						type="info"
						tooltipPlacement="left"
						className="nab-plan__site-id"
						text={ `${ _x(
							'Support Key',
							'text',
							'nelio-ab-testing'
						) }: ${ siteId }` }
					/>
				</h3>
				<div className="nab-plan__renewal">
					{ _x(
						'Youre currently using an agency subscription plan.',
						'user',
						'nelio-ab-testing'
					) }
				</div>
				<QuotaMeter />
			</div>
			<div className="nab-plan__actions">
				<DowngradeButton siteId={ siteId } />
				<ViewDetailsButton />
			</div>
		</div>
	);
};

// =======
// HELPERS
// =======

const DowngradeButton = ( { siteId } ) => {
	const [ isOpen, setIsOpen ] = useState( false );
	const { unlinkSite } = useDispatch( NAB_DATA );

	const open = () => setIsOpen( true );
	const close = () => setIsOpen( false );
	const confirm = () => {
		close();
		void unlinkSite( siteId );
	};

	return (
		<>
			<Button isDestructive className="nab-plan__action" onClick={ open }>
				{ _x(
					'Downgrade to Free Version',
					'command',
					'nelio-ab-testing'
				) }
			</Button>
			<ConfirmationDialog
				title={ _x(
					'Downgrade to Free version?',
					'text',
					'nelio-ab-testing'
				) }
				text={ _x(
					'This will remove the subscription license from the site and youll be using Nelio A/B Testings free version.',
					'text',
					'nelio-ab-testing'
				) }
				confirmLabel={ _x(
					'Downgrade',
					'command',
					'nelio-ab-testing'
				) }
				isOpen={ isOpen }
				onCancel={ close }
				onConfirm={ confirm }
			/>
		</>
	);
};

const ViewDetailsButton = () => {
	const expectedLicense = useSelect(
		( select ) => ( select( NAB_DATA ).getAccount() || {} ).license || ''
	);
	const [ isOpen, setIsOpen ] = useState( false );
	const [ license, setLicense ] = useState( '' );
	const { setPageAttribute } = useDispatch( NAB_DATA );

	const open = () => setIsOpen( true );
	const close = () => setIsOpen( false );
	const validate = () => {
		if ( ! expectedLicense || license !== expectedLicense ) {
			setLicense( '' );
			return;
		} //end if
		setPageAttribute( 'account/usesFullViewAgency', true );
		close();
	};

	return (
		<div>
			<Button isSecondary className="nab-plan__action" onClick={ open }>
				{ _x( 'View Details', 'command', 'nelio-ab-testing' ) }
			</Button>
			{ isOpen && (
				<Popover
					className="nab-license-form"
					noArrow={ false }
					position="bottom left"
				>
					<TextControl
						value={ license }
						placeholder={ _x(
							'Type your license here',
							'user',
							'nelio-ab-testing'
						) }
						className="nab-license-form__text-control"
						onChange={ setLicense }
					/>
					<Button
						isPrimary
						className="nab-license-form__button"
						onClick={ validate }
						disabled={ license.length !== expectedLicense.length }
					>
						{ _x( 'Validate', 'command', 'nelio-ab-testing' ) }
					</Button>
				</Popover>
			) }
		</div>
	);
};
