/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { useDispatch } from '@wordpress/data';
import { useState } from '@wordpress/element';
import { Popover, TextControl, Button } from '@wordpress/components';
import { _x } from '@wordpress/i18n';
import type { Popover as PopoverProps } from '@wordpress/components';

/**
 * External dependencies
 */
import { STORE_NAME as NAB_DATA } from '@nab/data';

/**
 * Internal dependencies
 */
import './style.scss';

const LICENSE_LENGTH = 21;
const OLD_LICENSE_LENGTH = 26;

export type LicensePopoverProps = {
	readonly isOpen?: boolean;
	readonly position: PopoverProps.Position;
	readonly onClick: () => void;
	readonly onFocusOutside: () => void;
};

export const LicensePopover = ( {
	isOpen,
	position,
	onClick,
	onFocusOutside,
}: LicensePopoverProps ): JSX.Element | null => {
	const [ license, setLicense ] = useState( '' );
	const { linkSite } = useDispatch( NAB_DATA );

	if ( ! isOpen ) {
		return null;
	} //end if

	return (
		<Popover
			className="nab-license-form"
			noArrow={ false }
			position={ position }
			onFocusOutside={ onFocusOutside }
		>
			<TextControl
				value={ license }
				placeholder={ _x(
					'Type your license here',
					'user',
					'nelio-ab-testing'
				) }
				maxLength={ Math.max( LICENSE_LENGTH, OLD_LICENSE_LENGTH ) }
				className="nab-license-form__text-control"
				onChange={ setLicense }
			/>
			<Button
				isPrimary
				className="nab-license-form__button"
				disabled={
					license.length !== LICENSE_LENGTH &&
					license.length !== OLD_LICENSE_LENGTH
				}
				onClick={ () => {
					onClick();
					void linkSite( license );
				} }
			>
				{ _x( 'Apply', 'command', 'nelio-ab-testing' ) }
			</Button>
		</Popover>
	);
};
