/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { Spinner } from '@wordpress/components';
import { useSelect } from '@wordpress/data';
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type { Site as SiteInstance } from '@nab/types';

/**
 * Internal dependencies
 */
import { Site } from '../site';
import './style.scss';

export const Sites = (): JSX.Element | null => {
	const isVisible = useIsVisible();
	const isLoading = useIsLoading();
	const sites = useSites();
	const sitesAllowed = useSitesAllowed();

	if ( ! isVisible ) {
		return null;
	} //end if

	const sitesConnected = sites.length;
	return (
		<div className="nab-account-container__box nab-sites">
			<h3 className="nab-sites__title">
				{ _x( 'Sites', 'text', 'nelio-ab-testing' ) }
				{ isLoading && <Spinner /> }
				<span className="nab-sites__availability">
					{ `${ sitesConnected } / ${ sitesAllowed }` }
				</span>
			</h3>

			<ul className="nab-sites__list">
				{ ! isLoading &&
					sites.map( ( site ) => (
						<Site key={ site.id } { ...site } />
					) ) }
			</ul>
		</div>
	);
};

// =====
// HOOKS
// =====

const useIsLoading = () =>
	useSelect(
		( select ) =>
			! select( NAB_DATA ).hasFinishedResolution( 'getSites', [
				select( NAB_DATA ).getSubscriptionId(),
			] )
	);

const useIsVisible = () =>
	useSelect( ( select ) => {
		const { getMode, getPageAttribute, isAgency } = select( NAB_DATA );
		const isSubscribed = getMode() === 'regular';
		const isAgencySummary =
			isAgency() && ! getPageAttribute( 'account/usesFullViewAgency' );
		return isSubscribed && ! isAgencySummary;
	} );

const useSites = (): ReadonlyArray< SiteInstance > =>
	useSelect(
		( select ) =>
			select( NAB_DATA ).getSites(
				select( NAB_DATA ).getSubscriptionId()
			) || []
	);

const useSitesAllowed = () =>
	useSelect( ( select ) => select( NAB_DATA ).getSitesAllowed() );
