/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { Popover, Button, Spinner, RadioControl } from '@wordpress/components';
import { useSelect, useDispatch } from '@wordpress/data';
import { useState } from '@wordpress/element';
import { _x } from '@wordpress/i18n';
import type { Popover as PopoverProps } from '@wordpress/components';

/**
 * External dependencies
 */
import { filter } from 'lodash';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import { getLocale } from '@nab/date';
import type { Currency, FSLocalizedString, SubscriptionPlan } from '@nab/types';

/**
 * Internal dependencies
 */
import './style.scss';

export type UpgradePopoverProps = {
	readonly isOpen?: boolean;
	readonly position: PopoverProps.Position;
	readonly onClick: () => void;
	readonly onFocusOutside: () => void;
};

export const UpgradePopover = ( {
	isOpen,
	position,
	onClick,
	onFocusOutside,
}: UpgradePopoverProps ): JSX.Element | null => {
	const isLoading = useIsLoading();
	const products = useProducts();
	const currency = useCurrency();
	const { upgradeSubscription } = useDispatch( NAB_DATA );
	const [ selectedOption, setSelectedOption ] = useState( '' );

	if ( ! isOpen ) {
		return null;
	} //end if

	if ( isLoading ) {
		return (
			<Popover
				className="nab-upgrade-form nab-upgrade-form--loading"
				noArrow={ false }
				position={ position }
				onFocusOutside={ onFocusOutside }
			>
				<Spinner />
			</Popover>
		);
	} //end if

	return (
		<Popover
			className="nab-upgrade-form"
			noArrow={ false }
			position={ position }
			onFocusOutside={ onFocusOutside }
		>
			<RadioControl
				className="nab-upgrade-form__product-container"
				label={ _x( 'Subscription Plans', 'text', 'nelio-ab-testing' ) }
				selected={ selectedOption }
				options={ products.map(
					( { id, displayName, price, description } ) => ( {
						label: ( (
							<div className="nab-upgrade-form__product">
								<strong className="nab-upgrade-form__product-name">
									{ localize( displayName ) }
								</strong>
								<span
									className="nab-upgrade-form__product-price"
									title={ currency }
								>
									{ formatPrice(
										price[ currency ],
										currency
									) }
								</span>
								<span className="nab-upgrade-form__product-description">
									{ localize( description ) }
								</span>
							</div> // eslint-disable-next-line @typescript-eslint/no-explicit-any
						 ) as any ) as string,
						value: id,
					} )
				) }
				onChange={ setSelectedOption }
			/>

			<div className="nab-upgrade-form__button-container">
				<Button
					isPrimary
					className="nab-upgrade-form__button"
					disabled={ ! selectedOption }
					onClick={ () => {
						onClick();
						void upgradeSubscription(
							selectedOption as SubscriptionPlan
						);
					} }
				>
					{ _x(
						'Upgrade Subscription',
						'command',
						'nelio-ab-testing'
					) }
				</Button>
			</div>
		</Popover>
	);
};

// =====
// HOOKS
// =====

const useIsLoading = () =>
	useSelect(
		( select ) =>
			! select( NAB_DATA ).hasFinishedResolution( 'getProducts' )
	);

const useProducts = () =>
	useSelect( ( select ) => {
		const { getProducts, getSubscriptionProduct } = select( NAB_DATA );

		const currentProduct = getSubscriptionProduct();
		const products = getProducts();
		return filter( products, ( { upgradeableFrom } ) =>
			upgradeableFrom.includes( currentProduct )
		);
	} );

const useCurrency = () =>
	useSelect( ( select ) => select( NAB_DATA ).getCurrency() || 'USD' );

// =======
// HELPERS
// =======

const formatPrice = ( price: number, currency: Currency ) =>
	'EUR' === currency ? `${ price }` : `$${ price }`;

const localize = ( str: FSLocalizedString ) => {
	const locale = getLocale();
	return locale.startsWith( 'es' ) ? str.es : str.en;
};
