/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { ExternalLink, Spinner } from '@wordpress/components';
import { useSelect } from '@wordpress/data';
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { STORE_NAME as NAB_DATA } from '@nab/data';

/**
 * Internal dependencies
 */
import { Invoice } from '../invoice';
import './style.scss';

export const Billing = (): JSX.Element | null => {
	const isVisible = useIsVisible();
	const isLoading = useIsLoading();
	const invoices = useInvoices();
	const urlToManagePayments = usePaymentsUrl();

	if ( ! isVisible ) {
		return null;
	} //end if

	return (
		<div className="nab-account-container__box nab-billing">
			<h3 className="nab-billing__title">
				{ _x( 'Billing History', 'text', 'nelio-ab-testing' ) }
				{ isLoading && <Spinner /> }
			</h3>

			{ ! isLoading && (
				<>
					<ExternalLink
						className="nab-billing__action"
						href={ urlToManagePayments }
					>
						{ _x( 'Manage Payments', 'user', 'nelio-ab-testing' ) }
					</ExternalLink>

					<table className="nab-billing__container">
						<thead>
							<tr>
								<th className="nab-billing__reference">
									{ _x(
										'Invoice Reference',
										'text (account, billing table title)',
										'nelio-ab-testing'
									) }
								</th>
								<th className="nab-billing__date">
									{ _x(
										'Date',
										'text (account, billing table title)',
										'nelio-ab-testing'
									) }
								</th>
								<th className="nab-billing__total">
									{ _x(
										'Total',
										'text (account, billing table title)',
										'nelio-ab-testing'
									) }
								</th>
							</tr>
						</thead>

						<tbody className="invoice-list">
							{ invoices.map( ( invoice ) => (
								<Invoice
									key={ invoice.reference }
									{ ...invoice }
								/>
							) ) }
						</tbody>
					</table>
				</>
			) }
		</div>
	);
};

// =====
// HOOKS
// =====

const useIsVisible = () =>
	useSelect( ( select ) => {
		const {
			getMode,
			getPageAttribute,
			isAgency,
			isSubscriptionDeprecated,
		} = select( NAB_DATA );

		const isSubscribed = getMode() === 'regular';
		const isDeprecated = isSubscriptionDeprecated();
		const isAgencySummary =
			isAgency() && ! getPageAttribute( 'account/usesFullViewAgency' );
		return isSubscribed && ! isDeprecated && ! isAgencySummary;
	} );

const useIsLoading = () =>
	useSelect(
		( select ) =>
			! select( NAB_DATA ).hasFinishedResolution( 'getInvoices', [
				select( NAB_DATA ).getSubscriptionId(),
			] )
	);

const useInvoices = () =>
	useSelect( ( select ) =>
		select( NAB_DATA ).getInvoices( select( NAB_DATA ).getSubscriptionId() )
	);

const usePaymentsUrl = () =>
	useSelect( ( select ) => select( NAB_DATA ).getUrlToManagePayments() );
