/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import '@wordpress/dom-ready';
import '@wordpress/notices';
import { dispatch } from '@wordpress/data';
import { render } from '@wordpress/element';

/**
 * External dependencies
 */
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type { SiteId } from '@nab/types';

/**
 * Internal dependencies
 */
import './style.scss';
import { renderHelp } from './help';

import { AccountProvider } from './components/provider';
import { Plan } from './components/plan';
import { AccountInfo } from './components/account-info';
import { Sites } from './components/sites';
import { Billing } from './components/billing';
import { LoadingModal } from './components/loading-modal';

type Settings = {
	readonly siteId: SiteId;
};

export function initPage( id: string, settings: Settings ): void {
	const { siteId } = settings;
	dispatch( NAB_DATA ).setPageAttribute(
		'account/usesFullViewAgency',
		false
	);

	const content = document.getElementById( id );
	if ( ! content ) {
		return;
	} //end if

	render(
		<AccountProvider>
			<Plan siteId={ siteId } />
			<AccountInfo />
			<Sites />
			<Billing />
			<LoadingModal />
		</AccountProvider>,
		content
	);

	renderHelp();
} //end initPage()
