/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { Button, Dashicon } from '@wordpress/components';
import { useSelect } from '@wordpress/data';
import { useState } from '@wordpress/element';
import { _x, sprintf } from '@wordpress/i18n';

/**
 * External dependencies
 */
import interpolateComponents from '@automattic/interpolate-components';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import { formatI18nDate } from '@nab/date';

/**
 * Internal dependencies
 */
import './style.scss';
import { LicensePopover } from '../license-popover';

export const AccountInfo = (): JSX.Element | null => {
	const isVisible = useIsVisible();
	const fullname = useFullName();
	const email = useEmail();
	const startDate = useStartDate();
	const license = useLicense();
	const photo = usePhoto();
	const [ isPopoverVisible, setPopoverVisible ] = useState( false );

	if ( ! isVisible ) {
		return null;
	} //end if

	return (
		<div className="nab-account-container__box nab-info">
			<h3 className="nab-info__title">
				{ _x(
					'Account Information',
					'title (account)',
					'nelio-ab-testing'
				) }
			</h3>

			<div className="nab-info__container">
				<div className="nab-info__profile">
					<div className="nab-info__picture nab-first-letter-a">
						<div
							className="nab-info__actual-picture"
							style={ {
								backgroundImage: `url(${ photo })`,
							} }
						></div>
					</div>
				</div>

				<div className="nab-info__details">
					<p className="nab-info__name">{ fullname }</p>
					<p className="nab-info__email">
						<Dashicon icon="email" className="nab-info__icon" />
						{ email }
					</p>
					<p className="nab-info__creation-date">
						<Dashicon icon="calendar" className="nab-info__icon" />
						{ interpolateComponents( {
							mixedString: sprintf(
								/* translators: date */
								_x(
									'Member since %s.',
									'text',
									'nelio-ab-testing'
								),
								`{{date}}${ formatI18nDate(
									startDate
								) }{{/date}}`
							),
							components: {
								date: <strong />,
							},
						} ) }
					</p>
					<div className="nab-info__license">
						<Dashicon
							icon="admin-network"
							className="nab-info__icon"
						/>
						<code
							title={ _x(
								'License Key',
								'text',
								'nelio-ab-testing'
							) }
						>
							{ license }
						</code>
						<div className="nab-info__change-license">
							<Button
								isTertiary
								onClick={ () => setPopoverVisible( true ) }
							>
								{ _x(
									'Change',
									'command',
									'nelio-ab-testing'
								) }
							</Button>
							<LicensePopover
								isOpen={ isPopoverVisible }
								position="bottom center"
								onClick={ () => setPopoverVisible( false ) }
								onFocusOutside={ () =>
									setPopoverVisible( false )
								}
							/>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

// =====
// HOOKS
// =====

const useIsVisible = () =>
	useSelect( ( select ) => {
		const { getMode, getPageAttribute, isAgency } = select( NAB_DATA );
		const isSubscribed = getMode() === 'regular';
		const isAgencySummary =
			isAgency() && ! getPageAttribute( 'account/usesFullViewAgency' );
		return isSubscribed && ! isAgencySummary;
	} );

const useFullName = () =>
	useSelect( ( select ) => select( NAB_DATA ).getFullname() );

const useEmail = () => useSelect( ( select ) => select( NAB_DATA ).getEmail() );

const useStartDate = () =>
	useSelect(
		( select ) =>
			select( NAB_DATA ).getStartDate() || select( NAB_DATA ).getToday()
	);

const useLicense = () =>
	useSelect( ( select ) => select( NAB_DATA ).getLicense() );

const usePhoto = () => useSelect( ( select ) => select( NAB_DATA ).getPhoto() );
