/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { NoticeList } from '@wordpress/components';
import { useSelect, useDispatch } from '@wordpress/data';
import { _x } from '@wordpress/i18n';
import { store as noticesStore } from '@wordpress/notices';

const NOTICES = noticesStore?.name ?? 'core/notices';

/**
 * External dependencies
 */
import { LoadingAnimation } from '@nab/components';
import { STORE_NAME as NAB_DATA } from '@nab/data';

export type AccountProviderProps = {
	readonly children: JSX.Element | JSX.Element[];
};

export const AccountProvider = ( {
	children,
}: AccountProviderProps ): JSX.Element => {
	const isLoading = useIsLoading();
	const notices = useNotices();
	const { removeNotice } = useDispatch( NOTICES );

	if ( isLoading ) {
		return (
			<LoadingAnimation
				text={ _x( 'Loading', 'text', 'nelio-ab-testing' ) }
			/>
		);
	} //end if

	return (
		<>
			<NoticeList
				notices={ notices }
				className="components-editor-notices__pinned"
				onRemove={ removeNotice }
			/>
			{ children }
		</>
	);
};

// =====
// HOOKS
// =====

const useIsLoading = () =>
	useSelect( ( select ) => {
		select( NAB_DATA ).getAccount();
		return ! select( NAB_DATA ).hasFinishedResolution( 'getAccount' );
	} );

import type { Notice } from 'wordpress__notices';
const useNotices = (): ReadonlyArray< Notice > =>
	useSelect( ( select ) => select( NOTICES ).getNotices() );
